.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.overlay-hidden {
  overflow: hidden;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 40;
  background-color: #00000080;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.overlay.active {
  opacity: 1;
  visibility: visible;
}

.modal {
  opacity: 0;
  visibility: hidden;
  z-index: 41;
  background-color: var(--modal-fon-color);
  border: 2px solid #f8d24e;
  border-radius: 10px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal.active {
  opacity: 1;
  visibility: visible;
}

.modal__cross {
  cursor: pointer;
  fill: currentColor;
  background-color: #0000;
  transition: transform .5s;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__cross:hover, .modal__cross:focus {
  color: var(--accent-color);
  transform: scale(1.5);
}

