
.about {
    padding-top: 60px;
    background-color: #84a178;
   
  }



  .about__submit {
    @include uppertitle-font(700, 18px, 1.22);
    text-align: center;
     margin-bottom: 12px;
     color: #FCFCFC;

     @media screen and (min-width:768px) and (max-width:1199px) {
        font-family: 'Montserrat';
font-style: normal;
font-weight: 800;
font-size: 22px;
line-height: 1.23;
letter-spacing: 0.04em;
text-transform: uppercase;
     }

     @media screen and (min-width:1200px) {
font-size: 30px;
line-height: 37px;        
     }
  }
  
  .about__title {
    @include h2-font-mob;
    text-transform: uppercase;
    color: #fcfcfc;
    text-align: center;
    margin-bottom: 54px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;

    
    
    @media screen and (min-width:768px) and (max-width:1199px) {
font-size: 36px;
line-height: 1.25;
max-width: 100%;
font-family: 'Seymour One';
font-style: normal;
font-weight: 400;
font-size: 34px;
line-height: 1.26;
letter-spacing: 0.06em;
margin-bottom: 79px;
    }

    @media screen and (min-width:1200px) {
        font-size: 36px;
line-height: 1.25;
max-width: 100%;
font-family: 'Seymour One';
font-style: normal;
font-weight: 400;
font-size: 34px;
line-height: 1.26;
letter-spacing: 0.06em;
margin-bottom: 121px;


        
    }
  }
  
  .about__thumb {
    display: block;
   position: relative;
   top: 0;
   bottom: 0;
   width: 280px;
   height: 280px;
   margin-left: auto;
   margin-right: auto;
  
  }
  
  .about__position {
    width: 280px;
    height: 280px; 
    margin-left: auto;
    margin-right: auto;
   position: absolute;
   bottom: 20px;
   background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
  box-shadow: 0px 4.48583px 16.8218px rgba(153, 114, 23, 0.25);
  border-radius: 140.182px;
  
  }
  
  .position__relative {
  
    width: 100%;
    height: 100%;
  
  
    background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
    box-shadow: 0px 5.30813px 19.9055px rgba(153, 114, 23, 0.25);
    border-radius: 50%;
  }
  
  .about__images {
    max-width: 280px;
    margin-left: auto;
    margin-right: auto;
    position: relative;

    @media screen and (min-width:768px) {
        max-width: 335px;
    }

    @media screen and (min-width:1200px) {
max-width: 490px;        
    }
  
  }
  
  
  .about__item {

    position: relative;

    @media screen and (min-width:1200px) {
     margin-right: 100px;   
    }

   
  }
  
  .about__shadow {
    width: 280px;
    height: 280px;
    bottom: 21px;
    right: 50%;
    transform: translateX(50%);
    background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
box-shadow: 0px 4.48583px 16.8218px rgba(153, 114, 23, 0.25);
border-radius: 50%;
margin-left: auto;
margin-right: auto;
position: absolute;
transition: background-color  var(--animation), transform var(--animation)  ;


&:hover {
  background-color: tomato;
}

@media screen and (min-width:768px) and (max-width:1199px) {
    width: 335px;
    height: 335px;
    bottom: 26px;
    right: 50%;
    transform: translateX(50%);
    background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
box-shadow: 0px 4.48583px 16.8218px rgba(153, 114, 23, 0.25);
border-radius: 50%;
position: absolute;
}

@media screen and (min-width:1200px) { 
    width: 500px;
    height: 500px;
    bottom: 30px;
    right: 50%;
    transform: translateX(50%);
    background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
box-shadow: 0px 4.48583px 16.8218px rgba(153, 114, 23, 0.25);
border-radius: 50%;
position: absolute;
    
}

  }

  .about__text { 
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.75;
    letter-spacing: 0.04em;
    color: #FCFCFC;
    text-align: center;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;
    margin-bottom: 4px;
    
    @media screen and (min-width:768px) and (max-width:1199px) {
     margin-top: 0px;
        text-align: start; 
      margin-bottom: 8px;
      max-width: 334px;
      margin-left: 0;
      font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 1.75;
letter-spacing: 0.04em;
    
    }

    @media screen and (min-width:1200px) {
        margin-top: 0px;
        font-size: 16px;
line-height: 1.62;
max-width: 463px;
text-align: start;
letter-spacing: 0.04em;
margin-bottom: 8px;
margin-top: 0;
        
    }
  
  }
  
  .about__text-two {
    font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 1.56;
text-align: center;
color: #FCFCFC;

letter-spacing: 0.04em;
    text-align: center;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
   
  
    @media screen and (min-width:768px) and (max-width:1199px) {
        @include special-font (400,12px,1.76,#FCFCFC);
      text-align: start;
      max-width: 334px;
      margin-left: 0;
      font-size: 12px;
      line-height: 1.75;
    }
   
        @media screen and (min-width:1200px) {
            @include special-font (400,12px,1.76,#FCFCFC);
            font-family: 'Montserrat';
            font-size: 14px;
            letter-spacing: 0.04em;
    line-height: 1.92;
    max-width: 463px;
    text-align: start;
             }

  }
  
  .about__arrow {
    margin-left: 4px;
    fill: currentColor;
    }

 

  .about__button {
    padding: 12px 34px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: calc(18 / 14);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px ;
  margin-left: auto;
  margin-right: auto;
  align-items: end;
  background-color: var(--accent-color);
  color: var(--black-text-color);
  border-radius: 10px;
  border: none;
  color: var(--black-text-color);
    transition: background-color var(--animation), color var(--animation);
  &:hover {
    background-color: var(--green-bnt-color);
  }
  &:focus {
    background-color: var(--green-bnt-color);
    color: var(--white-text-color);
    fill: #FCFCFC;
  }
  &--disable {
    background-color: var(--gray-text-color);
  }
  
  
    @media screen and (min-width:768px) {
  margin-left: 0px;
  margin-top: 48px;
  
    }
  }
 
  
  .about__description {
    font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.66;
  text-align: center;
  
  color: #FCFCFC;
  }
  
  .about__number {
    font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 1.16;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #FCFCFC;
  display: block;
  }
  
  .about__number::before {
    background-image: url('../images/about/icon/cowicon@1x.png');
    width: 50px;
    height: 50px;
    background-size: contain;
  }
  
  
  
  .about__list {
   display: block;
   margin: 0 auto;
  
   @media screen and (min-width:768px) and (max-width:1199px) {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 32px;
   
   }
@media screen and (min-width:1200px) {
    display: flex;
    align-items: center;
    margin-left: 16px;
}

  }
  
  .about__text-hidden {
  display: none;
  
    @media screen and (min-width:768px) {
        display: block;
      @include special-font (400,12px,1.76,#FCFCFC);
      width: 100%;
      margin-right: auto;
      margin-top: 8px;
      text-align: start; 
   visibility: visible;
   max-width: 336px;

}

   @media screen and (min-width:768px) and (max-width:1199px) {
    font-size: 14px;
line-height: 1.92; 
font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 1.91;
letter-spacing: 0.04em;
    }

    @media screen and (min-width:1200px) {
        @include special-font (400,12px,1.76,#FCFCFC);
            font-family: 'Montserrat';
            font-size: 14px;
            letter-spacing: 0.04em;
    line-height: 1.92;
    max-width: 457px;
    text-align: start;
        
    }
  }
  


  .form__container {

    padding-left: 60px;
    padding-right: 60px;
    padding-top: 32px;
    padding-bottom: 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @media screen and (min-width:768px) {
      padding-left: 60px;
      padding-right: 60px;
      padding-top: 32px;
      padding-bottom: 32px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      
    }
  }
  
  .about__modal {
    @media screen and (max-width:767px) {
      width: calc(100% - 40px);
      max-width: 440px; 
    z-index: 1000;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: #FCFCFC;
    border: 3px solid #F8D24E;
    border-radius: 10px;
        
  }
    @media screen and (min-width:768px) {
        max-width: 568px;      
        background-color: #FCFCFC;
        border: 3px solid #F8D24E;
        border-radius: 10px;
    }
  }

  .about__form-text {

    min-width: 224px;
    @media screen and (max-width:767px) {
      width: 100%;  
    font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 1.43;
color: #131313;
letter-spacing: normal;
        
  }
    @media screen and (min-width:768px) {
        max-width: 568px;    
        font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 1.34;
color: #131313;  
letter-spacing: normal;
    }
  }



  .about__form-title {
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.5;
    text-align: center;
    color: #131313;
   margin-bottom: 20px;

   @media screen and (min-width:768px)  {
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.4;
    text-align: center;
    margin-bottom: 32px;
    color: #131313;
    
   }

  }

  .about__close-btn {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    top: 2px;
    right: 2px;
    background-color: var(--modal-fon-color);
    cursor: pointer;
display: flex;
align-items: center;
justify-content: center;
  }

  .about__icon-close {
    fill: #131313;;
  }

  .about__video { 
    display: block;
    margin-bottom: 32px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    min-height: 199px;
    border-radius: 10px;
  

    @media screen and (min-width:768px) {
        width: 448px;
        margin-bottom: 32px;
    }

  }

