.backgroundtest {
  width: 100%;
  min-height: 1046px;
  background-image: url(../images/gallery/bg/sm-bg-@2x.jpg);

  // @media (min-device-pixel-ratio: 2),
  //   (min-resolution: 192dpi),
  //   (min-resolution: 2dppx) {
  //   background-image: url(../images/gallery/bg/sm-bg-@2x.jpg);
  // }

  top: 18%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: absolute;
  z-index: -1000;

  @media screen and (min-width: 480px) {
    min-height: 1046px;
    background-image: url(../images/gallery/bg/md-bg-@1x.jpg);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/gallery/bg/md-bg-@2x.jpg);
    }
    top: 18%;
  }

  @media screen and (min-width: 768px) {
    min-height: 958px;
    background-image: url(../images/gallery/bg/lg-bg-@1x.jpg);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/gallery/bg/lg-bg-@2x.jpg);
    }
    top: 50%;
  }

  @media screen and (min-width: 1200px) {
    min-height: 1020px;
    background-image: url(../images/gallery/bg/xlg-bg-@1x.jpg);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/gallery/bg/xlg-bg-@2x.jpg);
    }
    top: 22%;
    background-repeat: repeat-x;
  }
}

