.slider {
  position: relative;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 8px;
  z-index: 14;

  @media screen and (min-width: 480px) {
    border-radius: 18px;
  }

  @media screen and (min-width: 1200px) {
    border-radius: 24px;
  }
}
.slider__item {
}
.gallery__img {
  display: block;
  width: 100%;
  border-radius: 8px;

  @media screen and (min-width: $minmobile) {
    border-radius: 18px;
  }

  @media screen and (min-width: $mindesktop) {
    border-radius: 24px;
  }
}

