.products.section {
  padding-top: 60px;
  padding-bottom: 60px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;

  background-image: url(../images/gallery/bg/bcg2x.jpg);

  // @media screen and (min-width: 480px) {
  //   background-image: url(../images/gallery/bg/bcg2x.jpg);

  //   @media (min-device-pixel-ratio: 2),
  //     (min-resolution: 192dpi),
  //     (min-resolution: 2dppx) {
  //     background-image: url(../images/gallery/bg/bcg2x.jpg);
  //   }
  // }

  // tablet

  @media (min-width: $mintablet) {
    background-image: url(../images/gallery/bg/lg-bg-@1x.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/gallery/bg/lg-bg-@2x.jpg);
    }
  }

  // desktop

  @media (min-width: $mindesktop) {
    background-image: url(../images/gallery/bg/xlg-bg-@1x.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/gallery/bg/xlg-bg-@2x.jpg);
    }
  }
}

.products__content {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: $minmobile) {
  }

  @media (min-width: $mintablet) {
  }

  @media (min-width: $mindesktop) {
  }
}

.products__submit {
  text-transform: uppercase;
  color: var(--black-text-color);
  font-weight: 800;
  font-size: 18px;
  line-height: 1.22;

  @media (min-width: $minmobile) {
  }

  @media (min-width: $mintablet) {
    font-size: 22px;
    line-height: 1.22;
  }

  @media (min-width: $mindesktop) {
    font-size: 30px;
    line-height: 1.23;
    letter-spacing: 0.04em;
  }
}

.products__title {
  margin-top: 12px;
  font-family: 'Seymour One';
  font-size: 30px;
  line-height: 1.27;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  @media (min-width: $minmobile) {
  }

  @media (min-width: $mintablet) {
    font-size: 34px;
    line-height: 1.25;
  }

  @media (min-width: $mindesktop) {
    font-size: 36px;
    line-height: 1.25;
    letter-spacing: 0.06em;
  }
}

.products__items {
  display: flex;
  flex-direction: column;
  gap: 164px;
  margin-top: 148px;

  @media (min-width: $minmobile) {
  }

  @media (min-width: $mintablet) {
    flex-direction: row;
    margin-top: 200px;
    gap: 16px;
  }

  @media (min-width: $mindesktop) {
    margin-top: 303px;
    gap: 32px;
  }
}

.products__item--icecream {
  position: relative;
  margin-left: auto;
  margin-right: auto;

  max-width: 440px;
  height: 420px;
  padding-left: 40px;
  padding-right: 40px;
  background-color: var(--green-fon-color);
  border-radius: 24px;

  @media (min-width: $mintablet) {
    width: calc((100% - 32px) / 3);
    height: 428px;
    padding-left: 26px;
    padding-right: 26px;
  }

  @media (min-width: $mindesktop) {
    width: calc((100% - 64px) / 3);
    height: 535px;
    padding-left: 64px;
    padding-right: 64px;
  }
}

