.products__item--icecream::after {
  position: absolute;
  left: calc(50% - 100px);
  top: -120px;
  content: '';
  display: inline-block;
  background-size: contain;
  width: 200px;
  height: 200px;
  background-repeat: no-repeat;
  background-image: url('../images/products/sm-iceCream@1x.png');

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../images/products/sm-iceCream@2x.png');
  }

  // tablet screen

  @media screen and (min-width: $mintablet) {
    background-image: url('../images/products/md-iceCream@1x.png');

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/products/md-iceCream@2x.png');
    }
  }

  // desktop screen

  @media screen and (min-width: $mindesktop) {
    width: 300px;
    height: 300px;
    left: calc(50% - 150px);
    top: -183px;
    background-image: url('../images/products/lg-iceCream@1x.png');

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('../images/products/lg-iceCream@2x.png');
    }
  }
}

.products__item--icecream::before {
  position: absolute;
  left: calc(50% - 100px);
  top: -108px;
  content: '';
  display: inline-block;
  background-size: contain;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: linear-gradient(
      0deg,
      rgba(156, 190, 143, 0.7),
      rgba(156, 190, 143, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 5.34219px 20.0332px rgba(10, 136, 23, 0.25);

  // desktop screen

  @media screen and (min-width: $mindesktop) {
    width: 300px;
    height: 300px;
    left: calc(50% - 150px);
    top: -165px;
  }
}

