.products__item-title {
  padding-top: 123px;

  text-align: center;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  @media (min-width: $minmobile) {
  }

  @media (min-width: $mintablet) {
    padding-top: 107px;
    font-size: 22px;
    line-height: 1.23;
  }

  @media (min-width: $mindesktop) {
    padding-top: 165px;

    font-size: 30px;
    line-height: 1.23;
  }
}

.products__text {
  margin-top: 40px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.76;
  text-align: center;
  letter-spacing: 0.04em;

  @media (min-width: $mintablet) {
  }

  @media (min-width: $mindesktop) {
    margin-top: 65px;
    font-size: 16px;
  }
}

//    ( copy  hero)
.products__description-btn {
  position: absolute;
  bottom: 12px;
  left: calc(50% - 20px);
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  background-color: transparent;
  color: inherit;

  @media (min-width: $mindesktop) {
    bottom: 52px;
  }
}

.products__description-icon {
  height: 15px;
  fill: currentColor;
  transition: transform 500ms;

  .products__description-btn:hover & {
    transform: scaleX(1.3);
  }

  .products__description-btn:focus & {
    transform: scale(1.5);
  }
}
