.modal-franchise {
  // padding: 28px 30px;
  // width: 87.5%;
  // max-width: 440px;
  // background: var(--modal-fon-color);
  // border: 2px solid var(--yellow-border-color);
  // border-radius: 10px;

  // @media screen and (min-width: 768px) {
  //   padding: 32px 60px;
  //   max-width: 568px;
  // }
  padding: 28px 30px;
  width: 87.5%;
  max-height: 615px;
  height: calc(100vh - 50px);
  max-width: 440px;

  overflow-x: hidden;
  overflow-y: auto;
  background: var(--modal-fon-color);
  border: 2px solid var(--yellow-border-color);
  border-radius: 10px;

  @media screen and (min-width: 768px) {
    padding: 32px 60px;
    max-width: 568px;
    max-height: 685px;
  }
}
.modal-franchise__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  position: absolute;
  top: 8px;
  right: 8px;

  background: none;
  cursor: pointer;
}

.modal-franchise__title {
  margin-bottom: 20px;

  font-family: 'Seymour One';
  font-size: 16px;
  line-height: calc(24 / 16);
  color: var(--black-text-color);
  text-align: center;

  @media screen and (min-width: 768px) {
    width: 322px;
    margin: 0 auto;
    margin-bottom: 24px;

    font-size: 20px;
    line-height: calc(28 / 20);
  }
}
.modal-franchise__text {
  margin-bottom: 24px;

  font-family: 'Montserrat';
  font-size: 14px;
  line-height: 1.4;
  color: var(--black-text-color);

  @media screen and (min-width: 768px) {
    margin-bottom: 32px;

    font-size: 16px;
    line-height: 1.4;
    letter-spacing: normal;
  }
}
.form-franchise {
  display: flex;
  flex-direction: column;
  gap: 25px;

  @media screen and (min-width: 768px) {
    gap: 16px;
  }
}
.form-franchise__label {
  position: relative;

  @media screen and (min-width: 768px) {
    margin: 0 auto;
  }
}
.franchise-form__input {
  width: 100%;
  height: 32px;
  padding-left: 12px;

  background: rgba(248, 210, 78, 0.1);
  border: 1px solid var(--primary-fon-color);
  border-radius: 10px;
  outline: transparent;

  transition: border var(--animation);

  &::placeholder {
    font-size: 12px;
    line-height: 1.4;
    color: var(--black-text-color);
  }

  &:focus {
    border: 1px solid var(--yellow-border-color);
  }

  @media screen and (min-width: 768px) {
    width: 368px;
    height: 42px;
    padding-left: 16px;

    &::placeholder {
      opacity: 0;
    }
  }
}
.franchise-form__dsc {
  display: block;
  position: absolute;
  top: 50%;
  left: 16px;
  transform: translateY(-50%);

  font-size: 12px;
  line-height: 1.4;
  color: var(--black-text-color);

  transition: transform var(--animation), color var(--animation);

  .franchise-form__input:focus + &,
  .franchise-form__input:not(:placeholder-shown) + & {
    transform: translateY(-230%);
    color: var(--orange-text-color);
  }

  @media screen and (min-width: 768px) {
    .franchise-form__input:focus + &,
    .franchise-form__input:not(:placeholder-shown) + & {
      transform: translate(-170%, -50%);
      color: var(--orange-text-color);
    }
  }
}
.form-franchise__textarea {
  resize: none;
  width: 100%;
  height: 64px;
  padding-top: 8px;
  padding-left: 12px;
  outline: transparent;

  background: rgba(248, 210, 78, 0.1);
  border: 1px solid var(--primary-fon-color);
  border-radius: 10px;

  transition: border var(--animation);

  & ~ .franchise-form__dsc {
    top: 25%;
  }

  &:focus {
    border: 1px solid var(--yellow-border-color);
  }

  &:focus + .franchise-form__dsc,
  &:not(:placeholder-shown) + .franchise-form__dsc {
    transform: translateY(-230%);
    color: var(--brown-fon-color);
  }

  @media screen and (min-width: 768px) {
    width: 368px;
    height: 84px;

    transition: transform var(--animation), color var(--animation);

    &:focus + .franchise-form__dsc,
    &:not(:placeholder-shown) + .franchise-form__dsc {
      transform: translate(-140%, -55%);
      color: var(--brown-fon-color);
    }
  }
}
.franchise-btn {
  padding: 12px 34px;
  margin: 0 auto;
  display: inline-block;
  min-width: 132px;

  font-weight: 600;
  font-size: 14px;
  line-height: calc(17 / 14);
  color: var(--black-text-color);

  background: var(--yellow-border-color);
  border-radius: 10px;

  transition: background-color var(--animation), color var(--animation);

  &:hover {
    background-color: var(--green-bnt-color);
  }

  &:focus {
    background-color: var(--green-bnt-color);
    color: var(--white-text-color);
  }
}
